@import '../../global.scss';

$color-primary: #FFFDF7;
$color-secondary: #A2B0C5;
$color-dark: #222233;

$machi-light: #EEF5FF;

.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .intro {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 100px 20px 0;
        gap: 20px;

        .introText {
            
        }

        .introImg {
            border-radius: 16px;
            overflow: hidden;

            .img {
                display: block;
            }
        }
    }

    .wrapper {
        display: flex;
        flex-direction: column;
        padding: 100px 20px;

        .block {
            display: flex;
            flex-direction: column;
            width: 100%;
            margin: 50px 0;
            gap: 20px;

            .blockTitle {
                width: 100%;

                p {
                    font-size: 1.2em;
                }
            }

            .blockContent {
                width: 100%;
                
                display: flex;
                flex-direction: column;
                gap: 8px;

                p {
                    margin: 0;
                }

                .text1 {
                    font-size: 1em;
                    font-weight: 400;
                }

                .text2 {
                    font-size: 1.6em;
                    color: $machimachi-primary;
                }

                .text3 {
                    font-size: 1em;
                    font-weight: 300;
                }

                .contentCard {
                    display: flex;
                    flex-direction: column;
                    gap: 36px;

                    .textCard {
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        

                        .card {
                            background: var(--text-color);
                            color: var(--background-color);
                            padding: 24px;
                            border-radius: $radius-primary;
                            border: $machimachi-primary 4px solid;
                            display: flex;
                            flex-direction: column;
                            text-align: center;
                            justify-content: center;
                            height: 100%;
                        }
                    }
                }

                .tabs, .tabs2, .tabs3, .tabs4 {
                    display: flex;
                    flex-wrap: wrap;
                    width: 100%;

                    .tabsRadio {
                        display: none;
                    }

                    .tabsLabel {
                        padding: 16px 48px;
                        cursor: pointer;
                        color: var(--text-color);
                        background: $machimachi-primary;
                        border-radius: $radius-primary $radius-primary 0 0;
                    }
                      
                    .tabsContent {
                        order: 1;
                        width: 100%;
                        // border-bottom: 3px solid #dddddd;
                        // line-height: 1.5;
                        // font-size: 0.9em;
                        display: none;
                        background: var(--text-color);
                        color: var(--background-color);
                        padding: 48px;
                        border-radius: 0 $radius-primary $radius-primary $radius-primary;
                    }
                      
                    .tabsRadio:checked + .tabsLabel {
                        font-weight: bold;
                        color: $machimachi-primary;
                        background: var(--text-color);
                        // border-bottom: 2px solid #009578;
                    }
                      
                    .tabsRadio:checked + .tabsLabel + .tabsContent {
                        display: initial;
                    }
                }

                .imgContainer {
                    display: flex;
                    flex-direction: column;
                    gap: 24px;
                    flex-wrap: wrap;
                }

                .conclusion {
                    display: flex;
                    flex-direction: column;
                    gap: 24px;
                }

                .inlineLink {
                    color: $machimachi-primary;
                    text-decoration: underline;
                }
            }
        }
    }
}


@media screen and (min-device-width: 768px) {

.container {

    .intro {
        flex-direction: row;
        gap: 50px;
        max-width: 1000px;
        padding: 100px 50px;

        .introImg {
            flex: 1;
        }

        .introText {
            flex: 1;
        }
    }

    .nda {
        margin: 0;

        .icon {
            font-size: 2.5rem;
            width: 100%;
            max-width: 60px;
            height: 60px;
        }

        .bannerMobile {
            display: none;
        }

        .banner {
            display: flex;
            margin: 0 20px;
            background: $machimachi-primary;
            text-align: left;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            padding: 10px 20px;
            width: 100%;
            max-width: 1000px;
            border-radius: 16px;
            gap: 20px;
        }

        .banner:hover {
            filter: brightness(90%);
        }
    }

    .wrapper {
        max-width: 1000px;

        .block {
            flex-direction: row;
            gap: 48px;

            .blockTitle {
                flex: 250px;
                
                p {
                    margin: 0;
                    font-size: 1.6em;
                }
            }

            .blockContent {
                margin: 0;
                

                .text1 {
                    font-weight: 600;

                }

                .text2 {


                }

                .text3 {


                }

                video {
                    margin-top: 24px;
                }

                .contentCard {
                    flex-direction: row;

                    .textCard {
                        gap: 16px;

                        .card {
                            
                        }
                    }
                }

                .imgContainer {
                    flex-direction: row;
                    
                    img {
                        width: 45%;
                    }
                }
                  
            }
        }
    }
}
}