.index {
    display: flex;
    flex-direction: column;
    padding: 0 20px;
    // overflow: hidden;

    .header {
        height: 100vh;

        .content {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding: 50px 0;
            height: 100%;
            box-sizing: border-box;
            
            .intro {

                .title {
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                    align-items: center;

                    h1 {
                        margin: 0;
                        // font-size: 1.5em;
                    }

                    h3 {
                        margin: 0;
                    }

                    .darkModeToggle {
                        font-size: 2em;
                        color: inherit;
                        width: 100%;
                        max-width: 50px;
                        height: 100%;
                    }
                }
            }

            .more {
                display: flex;
                flex-direction: column;
                gap: 20px;

                .links {
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;

                    a {
                        text-decoration: underline;
                    }
                }

                .buttons {
                    display: flex;
                    flex-direction: column;
                    gap: 10px;
                    
                    Button {
                        border: 1px solid var(--button-background-hover);
                    }
                }
            }
        }
    }

    .workContainer {
        margin: 50px 0;
        display: flex;
        flex-direction: column;
        gap: 20px;

        .work {
            border-radius: 16px;
            overflow: hidden;
            padding: 20px;
            border: 1px solid var(--button-background-hover);
            background: var(--button-background-color);
            box-sizing: border-box;
            width: 100%;
            height: 320px;

            .text {
                display: flex;
                flex-direction: column;
                gap: 16px;
                
                .left {
                    display: flex;
                    flex-direction: column;
                    gap: 12px;

                    h2 {
                        font-weight: 400;
                        margin: 0;
                    }

                    .tags {
                        display: flex;
                        flex-wrap: wrap;
                        gap: 8px 16px;
                        
                        // opacity: 70%;

                        .tag {
                            color: var(--background-color);
                            background: var(--text-color);
                            padding: 4px 12px;
                            font-size: 0.8em;
                            text-wrap: nowrap;
                            border-radius: 16px;
                        }
                    }
                }

                .right {

                    
                }
            }

            .imgContainer {
                

                img {
                    // height: 100%;
                    // vertical-align: top;
                    position: relative;
                    bottom: -32px;
                }
            }

            div {
                background: inherit;
            }
        }

        p {
            margin: 0;
        }
    }

}

img {
    width: 100%;
}

